import { createSlice } from "@reduxjs/toolkit";

const cartSlice = createSlice({
  name: "cart",
  initialState: { items: [], user: null },
  reducers: {
    addItem(state, action) {
      const existingItem = state.items.find(
        (item) => item.id === action.payload.id
      );
      console.log("existing item", existingItem);
      if (existingItem) {
        // return console.log(existingItem);
        // const newitem = { ...action, quantity: ++1 };
        // return (state.items = state.items = [...state.items, action]);
        existingItem.quantity++;
      } else {
        const newActionItem = { ...action.payload, quantity: 1 };
        state.items = [...state.items, newActionItem];
      }
    },
    removeItem(state, action) {
      // state.items = state.items.filter((item) => {
      //   return item.id !== action.id;
      // });

      const existingItem = state.items.find(
        (item) => item.id === action.payload
      );
      if (existingItem.quantity > 1) {
        existingItem.quantity--;
      } else {
        state.items = state.items.filter((item) => item.id !== action.payload);
      }
      // let newItems = [...state.items];
      // if (index >= 0) {
      //   newItems.splice(index, 1);
      // } else {
      //   console.warn("cant't remove item!!!");
      // }
      // state.items = newItems;
    },
    deleteItem(state, action) {
      state.items = state.items.filter((item) => item.id !== action.payload);
    },
  },
});
export const cartActions = cartSlice.actions;
export default cartSlice;
