import "./App.css";
import Header from "./components/Header/Header";
import Home from "./components/Home/Home";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Checkout from "./components/Checkout/Checkout";
import Login from "./components/Login/Login";
import { auth } from "./firebasefile";
import { useSelector, useDispatch } from "react-redux";
import { userActions } from "./store/user-slice";
import { useEffect } from "react";
import Payment from "./components/Payment/Payment";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
const promise = loadStripe(
  "pk_test_51JiNdeHgGXNDVDsRQNomLzz3gFMD2BF6EPquZKQQTobbrnuPjZr7zMQKXeOxnTkXlOAEYmqqJw4Qm9CTXluB91sh00cakGtWMP"
);
// REMEMBER TO ADD REACT FLIP MOVE
//homework: upgrade the header component to include the user's email, add animation flipmove.
function App() {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user.userLogged);
  useEffect(() => {
    auth.onAuthStateChanged((authUser) => {
      if (authUser) {
        dispatch(
          userActions.setUser({ userLogged: authUser.multiFactor.user.email })
        );
      } else {
        dispatch(userActions.setUser({ userLogged: null }));
      }
    });
  }, []);

  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path="/" exact={true}>
            <Header />
            <Home />
          </Route>
          <Route path="/checkout">
            <Header />
            <Checkout />
          </Route>
          <Route path="/payment">
            <Header />
            <Elements stripe={promise}>
              <Payment />
            </Elements>
          </Route>
          <Route path="/login">
            <Login />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
