import React from "react";
import classes from "./Subtotal.module.css";
import CurrencyFormat from "react-currency-format";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
function Subtotal() {
  const items = useSelector((state) => state.cart.items);
  const pricesArr = items.map((item) => item.price * item.quantity);
  const prices = pricesArr.reduce((acc, cur) => {
    return acc + cur;
  }, 0);
  const quantityArr = items.map((item) => item.quantity);
  const quantity = quantityArr.reduce((acc, cur) => {
    return acc + cur;
  }, 0);

  return (
    <div className={classes.subtotal}>
      <CurrencyFormat
        renderText={(value) => {
          return (
            <>
              <p>
                Amount({items.length} product{items.length > 1 ? "s" : ""},
                total quantity: {quantity}):
                <strong>{value}</strong>
              </p>
              <small className={classes.subtotal__gift}>
                <input type="checkbox" />
                This order contains a gift
              </small>
            </>
          );
        }}
        decimalScale={2}
        value={prices}
        displayType={"text"}
        thousandSeparator={true}
        prefix={"$"}
      />
      <Link to="/payment">
        <button className={classes.subtotal__button}>
          Proceed to checkout
        </button>
      </Link>
    </div>
  );
}

export default Subtotal;
