import React from "react";
import classes from "./Payment.module.css";
import CheckoutProduct from "../Checkout/CheckoutProduct";
import CurrencyFormat from "react-currency-format";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
function Payment() {
  const items = useSelector((state) => state.cart?.items);
  const user = useSelector((state) => state.user?.userLogged);
  const pricesArr = items.map((item) => item.price * item.quantity);
  const prices = pricesArr.reduce((acc, cur) => {
    return acc + cur;
  }, 0);
  const quantityArr = items.map((item) => item.quantity);
  const quantity = quantityArr.reduce((acc, cur) => {
    return acc + cur;
  }, 0);

  const elements = useElements();
  const stripe = useStripe();
  return (
    <div className={classes.payment}>
      <div className={classes.payment__container}>
        <h1>
          <Link
            to="/checkout"
            style={{ color: "inherit", textDecoration: "underline" }}
          >
            Checkout ({items.length})
          </Link>
        </h1>
        <div className={classes.payment__section}>
          <div className={classes.payment__title}>
            <h1>Delivery Address</h1>
          </div>
          <div className={classes.payment__address}>
            <p>{user}</p>
            <p>Baker Street, 45</p>
            <p>Los Angeles, CA</p>
          </div>
        </div>
        <div className={classes.payment__section}>
          <div className={classes.payment__title}>
            <h1>Review items and delivery</h1>
          </div>
          <div className={classes.payment__items}>
            {items.map((item) => {
              return (
                <CheckoutProduct
                  key={item.id + Math.random()}
                  id={item.id}
                  image={item.image}
                  title={item.title}
                  price={item.price}
                  rating={item.rating}
                  quantity={item.quantity}
                />
              );
            })}
          </div>
        </div>
        <div className={classes.payment__section}>
          <div className={classes.payment__title}>
            <h2>Payment Method</h2>
          </div>
          <div className={classes.payment__details}>
            <form>
              <CardElement />
              <div className={classes.payment__priceContainer}>
                <CurrencyFormat
                  renderText={(value) => {
                    return (
                      <>
                        <h3>Total value: {value}</h3>
                      </>
                    );
                  }}
                  decimalScale={2}
                  value={prices}
                  displayType="text"
                  thousandSeparator={true}
                  prefix={"$"}
                />
                <button className={classes.payment__button}>Buy</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Payment;
