import React from "react";
import classes from "./CheckoutProduct.module.css";
import { useDispatch } from "react-redux";
import { cartActions } from "../../store/cart-slice";
import FlipMove from "react-flip-move";
function CheckoutProduct({ id, title, image, price, rating, quantity }) {
  const dispatch = useDispatch();
  const removeItemHandler = () => {
    dispatch(cartActions.removeItem(id));
  };
  const addItemHandler = () => {
    dispatch(cartActions.addItem({ id, title, image, price, rating }));
  };
  const deleteItemHandler = () => {
    dispatch(cartActions.deleteItem(id));
  };
  return (
    <div className={classes.checkoutProduct} key={id}>
      <img src={image} className={classes.checkoutProduct__image} />
      <div className={classes.checkoutProduct__info}>
        <p className={classes.checkoutProduct__title}>{title}</p>
        <p className={classes.checkoutProduct__price}>
          <small>$</small>
          <strong>{price}</strong>
        </p>
        <div className={classes.checkoutProduct__quantity}>
          <p>Quantity: {quantity}</p>
          <button onClick={addItemHandler}>+</button>
          <button onClick={removeItemHandler}>-</button>
        </div>

        <div className={classes.checkoutProduct__rating}>
          {Array(rating)
            .fill()
            .map((_, i) => {
              return <p key={i}>⭐</p>;
            })}
        </div>
      </div>
      <button
        onClick={deleteItemHandler}
        className={classes.checkoutProduct__button}
      >
        Remove From basket
      </button>
    </div>
  );
}

export default CheckoutProduct;
