import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import classes from "./Login.module.css";
import { auth } from "../../firebasefile";

function Login() {
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isRegistering, setIsRegistering] = useState(false);
  const registerHandler = (e) => {
    e.preventDefault();
    auth
      .createUserWithEmailAndPassword(email, password)
      .then((data) => console.log(data))
      .catch((e) => alert(e.message));
  };
  const loginHandler = (e) => {
    e.preventDefault();
    auth
      .signInWithEmailAndPassword(email, password)
      .then((data) => history.replace("/"))
      .catch((e) => alert(e.message));
  };
  return (
    <div className={classes.login}>
      <Link to="/">
        <img
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a9/Amazon_logo.svg/800px-Amazon_logo.svg.png"
          className={classes.login__logo}
        />
      </Link>
      <div className={classes.login__container}>
        <h1>{!isRegistering ? "Sign In" : "Register"}</h1>
        <form>
          <h5>E-mail</h5>
          <input
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <h5>Password</h5>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          {isRegistering ? (
            <button
              className={classes.login__signInButton}
              onClick={registerHandler}
              type="submit"
            >
              Register
            </button>
          ) : (
            <button
              className={classes.login__signInButton}
              onClick={loginHandler}
              type="submit"
            >
              Sign In
            </button>
          )}
        </form>
        <p className={classes.login__paragraph}>
          By continuing, you agree to <strong>AMAZON FAKE CLONE</strong>. Please
          check the Privacy Notice, Cookie Notice and Interest-Based Ads Notice.
        </p>
        {!isRegistering ? (
          <button
            className={classes.login__registerButton}
            type="button"
            onClick={(e) => setIsRegistering(true)}
          >
            New? Register Now
          </button>
        ) : (
          <button
            className={classes.login__registerButton}
            type="button"
            onClick={(e) => setIsRegistering(false)}
          >
            Have an Account? Login now!
          </button>
        )}
      </div>
    </div>
  );
}

export default Login;
