import React from "react";
import classes from "./Checkout.module.css";
import Subtotal from "./Subtotal";
import { useSelector } from "react-redux";
import CheckoutProduct from "./CheckoutProduct";

function Checkout() {
  const items = useSelector((state) => state.cart.items);
  const user = useSelector((state) => state.user.userLogged);
  console.log("items", items);
  return (
    <div className={classes.checkout}>
      <div className={classes.checkout__left}>
        <div className={classes.checkout__adContainer}>
          <img
            alt="banner"
            src="https://www.junglescout.com/wp-content/uploads/2020/05/Prime-day-banner.png"
            className={classes.checkout__ad}
          />
        </div>
        <div>
          <h3>Hello, {user}</h3>
          <h2 className={classes.checkout__title}>Your shopping Basket</h2>

          {items.length === 0 && (
            <h3 className={classes.checkout__subtitle}>No items yet!</h3>
          )}
          {items.map((item) => {
            return (
              <CheckoutProduct
                key={item.id + Math.random()}
                id={item.id}
                image={item.image}
                title={item.title}
                price={item.price}
                rating={item.rating}
                quantity={item.quantity}
              />
            );
          })}
        </div>
      </div>
      <div className={classes.checkout__right}>
        <Subtotal />
      </div>
    </div>
  );
}

export default Checkout;
